.Login {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.Login .LoginTitle {
  font-size: 10vh;
  color: #ffa38b;
}

.Login .cols {
  display: flex;
}

.Login .col1 {
  border-right: 0.5px solid #777777;
  width: 50%;
}

.Login .col2 {
  width: 50%;
}

.Login a:hover {
  text-decoration: underline;
  cursor: pointer;
}

.Login a {
  font-weight: bold;
}